.input {
  padding: 10px;
  outline: none;
  border: none;
  color: #080232;
  font-size: 15px;
  font-weight: 500;
  max-width: 400px;
  width: 100%;
  margin: 10px;
  border-bottom: 1px solid #080232;

  &:hover, &:focus {
    border-bottom: 1px solid #55608f;
    color: #55608f;
  }
}
.checkbox {
  margin-bottom: 10px;
}
