* {
  box-sizing: border-box;
  margin: 0;
  padding:0;
  font-family: 'Montserrat', sans-serif;
}

.App {
  position: relative;
  width: 100%;
}

.page-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

.page {
  margin: 50px 20px;
  padding: 10px 25px;
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  min-height: 70vh;

}

.wrapper {
  display: flex;
}

table {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 6px;
  border-spacing: 5px;
  border-collapse: collapse;
  overflow: hidden;
  background: linear-gradient(45deg, #49a09d, #080232);
  color: #fff;
}

th, td {
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.2);
}

th {
  text-align: left;
}

thead {
  background-color: #55608f;
  width: 100%;
}

td {
  padding: 15px;
  background-color: rgba(167, 144, 144, 0.2);
  color: #fff;

  .button {
    color: #fff;
    cursor: pointer;
  }

  span {
    cursor: pointer;
  }
}

@media (max-width: 600px) {
  .page{
    display: none;
  }
}


